












































































































































import Vue from 'vue'
import Catalogue from "@/config/catalogue"
import axios from "axios"

const URL = "https://discord.com/api/webhooks/825829069706100746/fmjDuJjIZuQBEG_mdO_dv0hlrLyYxjh6B-2_w0UmECoUMQkxv88UVP7M_qjAxduYmzmP"


export default Vue.extend({
    data: function()
    {
        return {
            category: Catalogue,
            showForm: false,

            targetBuy: "",
            priceBuy: 0,

            name: "",
            phone: "",
            mail: "",
            licences: {car: false, moto: false, truck: false},

            sended: false,
            error: ""
        }
    },
    methods: {
        showModal: function(carName: string, price: number) {
            this.showForm = true
            this.targetBuy = carName
            this.priceBuy = price
        },

        onSubmit: async function() {
            if(this.name == "") { 
                this.error = "Veuillez indiquer votre nom" 
                return
            }
            
            if(this.phone == "") { 
                this.error = "Veuillez indiquer votre numéro de téléphone" 
                return
            }

            if(this.mail == "") { 
                this.error = "Veuillez indiquer votre adresse mail" 
                return
            }

            this.error = ""
            this.showForm = false

            const toSend = {
                "content": "",
                "embeds": [
                    {
                    "description": `**Nom du client**: ${this.name}\n**Numéro de téléphone du client**: ${this.phone}\n**Email du Client**: ${this.mail}\n\n__Licences__\n- **Voiture**: ${Boolean(this.licences.car) == true && "Oui" || "Non"}\n- **Moto**: ${Boolean(this.licences.moto) == true && "Oui" || "Non"}\n- **Poids Lourds**: ${Boolean(this.licences.truck) == true && "Oui" || "Non"}\n\n__Véhicule demandé__\n**Modèle**: ${this.targetBuy}\n**Prix**: $${Number(this.priceBuy).toLocaleString()}`,
                    "color": 5814783
                    }
                ]
            }

            const res = await axios.post(URL, toSend, {headers: {"Content-Type": "application/json"}}).then(() => this.sended = true)

            setTimeout(() => {
                this.targetBuy = ""
                this.priceBuy = 0

                this.name = ""
                this.phone = ""
                this.licences = {car: false, moto: false, truck: false}

                this.showForm = false
                this.sended = false
                this.error = ""
            }, 5000);
        }
    }
})
